<template>
  <div class="wrapper">
    <div class="content">
      <div class="c-top">
        <div class="ct-left clearfloat">
          <ctitle :title="'信访量信访形式占比'"></ctitle>
          <allCount class="all-count" :count="allcount"></allCount>
          <div class="inner">
            <div class="detail">
              <detailList :params="detailList"></detailList>
            </div>
            <div class="chart">
              <pie-chart3D :params="zbPieParams"></pie-chart3D>
            </div>
          </div>
        </div>
        <div class="ct-right">
          <ctitle :title="'信访量信访形式走势'"></ctitle>
          <div class="inner">
            <div class="detail" v-loading="!xfxszsList.length"
                 :element-loading-background="$STANDARD.loadingBgColor">
              <p v-if="xfxszsList.length">年增减率</p>
              <div
                v-for="(item, index) in xfxszsList"
                :key="index"
              >
                <label>{{item.name}}</label>
                <span class="perCount">
                <span
                  class="countSymbol"
                  v-if="item.value > 0"
                >+</span>
                  <!--<incNumber class="number allCount" :incNumber="item.value"></incNumber>-->
                  <span class="number allCount">{{(item.value*100).toFixed(2)}}</span>
                <span class="unit">%</span>
              </span>
              </div>
            </div>
            <timeChange class="timeChange" @change="zsTimeClick"></timeChange>
            <LinesR class="chart" :params="zsLineParams"></LinesR>
          </div>
        </div>
      </div>
      <div class="c-bottom">
        <ctitle :title="'信访量四级登记机构-信访形式占比'"></ctitle>
        <div class="inner">
          <div class="chart" v-for="(item,index) of djjgArr" :key="index">
            <loopPie :params="item.params"></loopPie>
            <p class="label">{{item.name}}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import ctitle from "@/components/title/index";
  import timeChange from "@/components/timeChange/index";
  import incNumber from "@/components/incNumber/index";
  import loopPie from "@/components/pie/LoopPie";
  import PieChart3D from "@/components/pie/PieChart3D";
  import LinesR from "@/components/line/LinesR";
  import allCount from "@/components/list/allCount";
  import detailList from "@/components/list/detailList";

  export default {
    name: "xfxs",
    components: {
      ctitle,
      timeChange,
      incNumber,
      loopPie,
      PieChart3D,
      LinesR,
      allCount,
      detailList
    },
    data() {
      return {
        // 信访形式占比
        allcount: -1,
        detailList: null,
        zbPieParams: {
          data: [],
          color: ["#5166c1", "#32b1e0", "#15bbc2"],
          angle: 20,
          radius: "40%",
          labelRadius: 15,
          styleObj: {
            width: "100%",
            height: "100%"
          },
          isBr: true,
          labelPer: true
        },
        // 信访形式走势
        xfxszsList: [],
        zsLineParams: {
          data: [],
          colors: [
            {
              line: "#e11770",
              start: "rgba(225, 23, 112, .5)",
              end: "rgba(225, 23, 112, .5)"
            },
            {
              line: "#ffb435",
              start: "rgba(255, 180, 53, .5)",
              end: "rgba(255, 180, 53, .5)"
            },
            {
              line: "#1a82f7",
              start: "rgba(26, 130, 247, .5)",
              end: "rgba(26, 130, 247, .5)"
            },
            {
              line: "#a1d871",
              start: "rgba(161, 216, 113, .5)",
              end: "rgba(161, 216, 113, .5)"
            },
            {
              line: "#ff825b",
              start: "rgba(255, 130, 91, .5)",
              end: "rgba(255, 130, 91, .5)"
            }
          ],
          option: {
            splitNumber:4,
            splitLineShow: true,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisCategoryName: "年",
            axisYName:"件次",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: true,
            areaShow: 0,
            smoothMonotone: "none",
            dataZoomshow: false,
            legendIcon: "rect",
            left: "8%",
            right: "15%",
            top: "15%",
            bottom: "10%",
            legendTop: "2%",
            legendRight: "15%"
          },
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          map: {},
          xName: "name"
        },
        //登记机构占比
        djjgArr: [
          {name: '国家', params: {data: [],options:{}}},
          {name: '省级', params: {data: [],options:{}}},
          {name: '市级', params: {data: [],options:{}}},
          {name: '县级', params: {data: [],options:{}}}
        ],
        djjgPieParams: {
          options: {
            colors: this.$STANDARD.gradientColors,
            radius: ["58%", "78%"],
            center: ["50%", "50%"],
            centerRadius: ["45%", "46%"],
            labelRich: {
              b: this.fontSize(0.26),
              d: this.fontSize(0.28),
              bHeight: this.fontSize(0.4)
            },
            length: 20,
            length2: 70,
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rollFlag: true
          }
        }
      };
    },
    mounted() {
      this.getAPI(this.year);
    },
    methods: {
      getAPI(year) {
        // 1.信访形式占比（总数）
        get_http("zlfxXfxsZbtotal", {year: year})
          .then(res => {
            this.allcount = res.data.data[0].value;
          })
          .catch(err => {
            console.log(err);
          });
        // 2.信访形式占比（各类型）
        get_http("zlfxXfxsZbType", {year: year})
          .then(res => {
            this.detailList = res.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 3.信访形式占比（饼图）
        get_http("zlfxXfxsZbPie", {year: year})
          .then(res => {
            this.zbPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 4信访形式走势（各类型）
        get_http("zlfxXfxsZsType", {year: year})
          .then(res => {
            this.xfxszsList = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 5信访形式走势
        this.getZsLineAPI(year, '年度');
        // 6四级登记机构-信访形式占比（饼图）
        this.getDjjgZbPieAPI(year);
      },
      // 四级登记机构-信访形式占比（饼图）
      getDjjgZbPieAPI(year) {
        for (let item of this.djjgArr.values()) {
          item.params.options = this.djjgPieParams.options;
          get_http("zlfxXfxsJgPie", {year: year, djjg: item.name})
            .then(res => {
              item.params.data = res.data.data;
            })
            .catch(err => {
              console.log(err);
            });
        }
      },
      // 信访形式走势
      getZsLineAPI(year, time) {
        let timetype = '', _year = '';
        if (time === '年度') {
          timetype = 'cy';
          _year = '[now-4, now]'
        } else {
          timetype = 'cm';
          _year = year
        }
        get_http("zlfxXfxsZsLine", {year: _year, timetype: timetype})
          .then(res => {
            this.zsLineParams.option.axisCategoryName = (time === '年度' ? '年' : '月');
            this.zsLineParams.map = res.data.map;
            this.zsLineParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 信访形式走势-时间切换
      zsTimeClick(time) {
        this.getZsLineAPI(this.year, time);
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      },
    },
    computed: {
      year() {
        return this.$store.state.year;
      }
    },
    watch: {
      year(val) {
        this.getAPI(val);
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 80px
    height calc(100% - 80px)

    .content
      width 100%
      height 100%
      display flex
      flex-direction column
      padding 20px 30px
      box-sizing border-box

      .c-top
        flex 1
        display flex

        .ct-left
          flex 1
          height 100%
          position relative
          border-right 1px #91e3f7 dashed
          font-size 0
          .all-count
            height 0.48rem
          .inner
            display flex
            width 100%
            height calc(100% - 0.75rem)
            .detail
              flex 1
              display flex
              justify-content center
              align-items center
              width 100%
              height 100%
            .chart
              flex 1
              height 2.7rem
        .ct-right
          flex 1
          height 100%
          .inner
            display flex
            height calc(100% - 0.28rem)
            position relative
            .timeChange
              top -0.3rem
              right 0.9rem
            .chart
              flex 3
            .detail
              flex 1
              box-sizing border-box
              padding 0.8rem 0 0 0.7rem
              p
                font-size 0.18rem
                color #00b0ff
                margin-bottom 0.2rem
              div
                display flex
                line-height .4rem
                label
                  color #fff
                  font-size 0.16rem
                  width 0.8rem
                  display inline-block
                  margin-right 0.1rem
                  text-align right

                .perCount
                  font-size 0
                  color #ffc833

                  .countSymbol
                    font-size 0.16rem
                    color #ffc833

                  .number
                    display inline-block
                    flex 1
                    font-size 0.16rem
                    color #ffc833
                    margin-bottom 0.1rem

                  .unit
                    font-size .16rem
      .c-bottom
        flex 1
        background url('../../../assets/c-bottom-bg.png') no-repeat center
        background-size 100% 100%
        position relative
        box-sizing border-box
        padding .4rem .3rem 0 .3rem
        margin 0 .2rem .1rem .2rem
        .inner
          display flex
          width 100%
          height calc(100% - 0.7rem)
          .chart
            position relative
            flex 1
            .label
              text-align center
              color #fff
              font-weight 700
              font-size 0.2rem
</style>
